<template>
    <section>

        <md-dialog-alert
            :md-active.sync="displayError"
            :md-content="errorMessage"
            md-confirm-text="ok" />

        <form>
            <md-field>
                <label>Enter your email</label>
                <md-input type="email" v-model="email" placeholder="Email" />
            </md-field>

            <md-field>
                <label>Enter your password</label>
                <md-input type="password" v-model="password" placeholder="Password" />
            </md-field>
            <md-button type="submit" class="md-raised md-primary" @click="onLogin">Log in</md-button>
        </form>


    </section>
</template>

<script>
import CustomButton from "@/components/customButton";

export default {
    name: "LoginPage",
    components: {
        CustomButton
    },
    data() {
        return {
            email: '',
            password: '',
        }
    },
    methods: {
        onLogin(e) {
            e.preventDefault();

            if (this.email === '' || this.password ===''){
                return this.displayCustomError('fill email and password');
            }

            const credentials = {
                email: this.email,
                password: this.password
            }

            this.$emit('on-login', credentials);
        }
    }
}
</script>

